.card__like {
    width: 22px;
    height: 19px;
    background-image: url(../../../images/like_heart.svg);
    background-color: transparent;
    border: 0;
    background-size: 22px 19px;
}

.card__like:hover {
    opacity: .5;
    transition: opacity .5s ease-in;
    cursor: pointer;
}